import React, { Component } from "react";
import P5Wrapper from "react-p5-wrapper";
import sketch from "./sketches/sketch";
import UploadButton from "./UploadButton";
import Output from "./Output";
import "bootstrap/dist/css/bootstrap.min.css";
import "./canvas.css";
import Popup from "./popup.js";
import "./popup.css";

let savedImage = "";

class Canvas extends Component {
    constructor() {
        super();
        this.state = {
            isOutput: null,

            color: false,
            evaluate: false,
            equation: "",
            formatted_equation: "",
            result: "",
        };
    }

    pseudo = (data) => {
        savedImage = data;
        console.log("###" + data);
    };

    sendImgToServer = (image) => {
        this.setState({ isOutput: "loading" });
        console.log("req:", image);
        let img = image.replace("data:image/png;base64,", "");

        let data = {};
        data.image = img;

        //*************************************************

        // FOR RUNNING LOCALLY, CHANGE TO 0.0.0.0
        // USE IP OF PC WHEN SERVING TO MOBILE
        // RUN flask run --host=0.0.0.0
        // FOR IP RUN ip addr

        const SERVER = "139.59.5.80:5001";

        //*************************************************
        fetch(`http://${SERVER}/predict`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        })
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                let data1 = JSON.parse(data);
                console.log("Success:", data1);
                this.setState({
                    isOutput: true,
                    color: false,
                    evaluate: false,
                    equation: data1["Entered_equation"],
                    formatted_equation: data1["Formatted_equation"],
                    result: data1["solution"],
                });
                console.log(this.state);
            })
            .catch((error) => {
                this.setState({ isOutput: null });
                console.error("Error:", error);
                alert("Sorry, we couldn't process this...try again");
                this.onClear();
            });
    };

    onClear = () => {
        this.setState({
            color: true,
            evaluate: null,
            equation: "",
            formatted_equation: "",
            result: "",
        });
    };

    onEval = () => {
        this.setState({
            color: false,
            evaluate: true,
            equation: "",
            formatted_equation: "",
            result: "",
        });
    };

    render() {
        return (
            <div className="container ">
                <br />
                <div className="row">Draw the equation below</div>
                <br />
                <div className="canvaspart">
                    <div className="canvas2">
                        <P5Wrapper
                            sketch={sketch}
                            color={this.state.color}
                            evaluate={this.state.evaluate}
                            callBack={this.pseudo}
                        ></P5Wrapper>
                    </div>
                    <br />
                    <div className="buttonpart">
                        <div className="col1">
                            <button
                                type="button"
                                onClick={
                                    this.state.evaluate
                                        ? () => this.sendImgToServer(savedImage)
                                        : this.onEval
                                }
                                className="savebutton"
                            >
                                evaluate
                            </button>

                            <button
                                type="button"
                                onClick={this.onClear}
                                className="clearbutton"
                            >
                                Clear
                            </button>
                        </div>
                    </div>
                </div>
                <br />
                <br />

                <div className="belowpart">
                    <div className="offset-md-3">
                        <UploadButton sendImgToServer={this.sendImgToServer} />
                    </div>
                </div>

                {this.state.isOutput == "loading" ? (
                    <h1>Loading</h1>
                ) : this.state.isOutput == null ? null : (
                    <Popup>
                        <div className="box">
                            <span
                                className="close-icon"
                                onClick={() =>
                                    this.setState({ isOutput: null })
                                }
                            >
                                x
                            </span>

                            <Output
                                equation={this.state.equation}
                                formatted_equation={
                                    this.state.formatted_equation
                                }
                                result={this.state.result}
                            />
                        </div>
                    </Popup>
                )}
            </div>
        );
    }
}

export default Canvas;
