import React, { Component } from 'react';
import {Button,Modal,Image,Alert} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {dataURLtoFile,filetoDataURL} from 'image-conversion';

let imgBase64 = '';

class UploadButton extends Component {
    constructor(props){
        super(props);
        this.state = {
            uploadState: true,
            imgUrl: '',
            uploadError: false,
            showModal: false
        };
    }

    validateFile = async (event) => {
        let extension = event.target.files[0].name.substring(event.target.files[0].name.lastIndexOf('.')+1).toLowerCase();
        let img = await filetoDataURL(event.target.files[0]);
        if(extension === 'jpg' || extension === 'png' || extension === 'jpeg'){

	    const file = await dataURLtoFile(img,'image/png');
            let reader = new FileReader();
            reader.onloadend = () => {
                imgBase64 = reader.result;
                this.setState(Object.assign(this.state,{
                    uploadState:false,
                    imgUrl:img,
                    uploadError: false,
                    showModal: true
                }));
                this.props.sendImgToServer(imgBase64)
                console.log(this.state.imgBase64);

            }
            reader.readAsDataURL(file);
        }
        else {
            imgBase64 = '';
            this.setState(Object.assign(this.state,{
                uploadState:true,
                imgUrl:'',
                uploadError: true,
                showModal: true
            }));
        }
        event.target.value= null;
    }

    handleClose = () => {
        this.setState(Object.assign(this.state,{showModal:false}));
    }

    modalBody = () => {
        if(!this.state.uploadError) {
            return (
                <div>
                    <Image alt="selected image" src={this.state.imgUrl} fluid/>
                </div>
            );
        }
        else{
            return(
                <div>
                    <Alert variant="danger">
                        The selected file is not an image. Please select an image
                    </Alert>
                </div>
            );
        }
    }
	                //<input type="file" id='upload-file' onChange={this.validateFile} name='Upload' /> 
    render() {
        return (
            <div>
		<label className="btn btn-default btn-file">
    			Upload <input type="file" style={{display: 'none'}} onChange={this.validateFile} required/>
		</label>
                   
            </div>
        );
    }
}

export default UploadButton;
